
import Tab from '../../components/public/navbar'
import axios from 'axios'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: "me",
directives: {
TransferDom
},
components:{Tab,XButton,Alert,TransferDom},
data(){
return{
openSimple: false,
show:false,
img_url:"",
user_name:"",
c:"",
isFirst:true
}
},
created(){
console.log("回来了1")
if(this.isFirst){
this.getData();
this.getCode();
}
},
activated(){
console.log("回来了2")
},
methods:{
openSimpleDialog () {
this.openSimple = true;
},
closeSimpleDialog () {
this.openSimple = false;
},
getData(){
axios.get("http://e.shushangsoft.com/customerLoginController.do?method=getCustomerInfo&loginType=2").then((res)=>{
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("登录信息过期")
this.$store.commit('updateFirst', false);
this.$router.replace({
path: '/login',query:{}
});
this.$destroy();
}else {
console.log(res.data.data)
this.isFirst=false;
this.user_name=res.data.data.customerName;
this.$store.commit("updateIsLogin",true);
this.$store.commit('updateFirst', false);
}
})
},

getCode(){
axios.get("http://e.shushangsoft.com/customerLoginController.do?method=getBarCode&type=2&loginType=2").then((res)=>{
this.img_url=res
})
},
refreshImg(){
this.$refs.qrcode.src="http://e.shushangsoft.com/customerLoginController.do?method=getBarCode&type=2&loginType=2"+"&_r="+Math.random()
console.log("http://e.shushangsoft.com/customerLoginController.do?method=getBarCode&type=2&loginType=2"+"&_r="+Math.random())

},
mypaper(){
this.$router.push({
path: `/gitpaper`
})
},
myGame(){
this.$router.push({
path: `/gamelist`
})
},
exchange(){
this.$router.push({
path: `/exchangepaper`
})
}
},
beforeDestroy(){
this.$store.commit("updateIsLogin",true);
},
}



