import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
import Loading from '../../components/loading/loading'
export default {
  name: "gift-paper",
  components:{Scroll,Loading},
  data(){
    return{
      id:"",
      pullup: true,
      dataList:[]
    }
  },
  created(){
    this.id=this.$route.params.id;
    this.getData(this.id)
  },

  methods:{
    searchMore() {
      if (this.dataList.length > 5) {
        this.$refs.loading.style.display = "flex"
        this.loading = true;
        setTimeout(() => {
          let last = this.dataList[this.dataList.length - 1];
          for (let i = 1; i <= 10; i++) {
            this.dataList.push(last + i);
          }
          this.$refs.loading.style.display = "none"
          this.loading = false;
        }, 2500);
      }
      else {
        this.$refs.loading.style.display = "none"
        this.loading = false;
      }
    },
    getData(id){
      axios.get("http://e.shushangsoft.com/customerLoginController.do?method=getMerchantsByIndustryCategoryId&industryType="+id+"&loginType=2").then((res)=>{
        console.log(res.data)
        this.$refs.loading.style.display = "none"
        if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
          alert("登录信息过期")
          this.$router.replace({
            path: '/login',query:{}
          });
        }else {
          this.dataList=res.data.dataList
        }
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item.merchantId}`
      })
    },
    getImg(item){
      if(this.dataList.length!==0){
        var imgId=item.imageIds.split(",");
        return "http://192.168.0.55:8899/fileController.do?method=showimage&id="+imgId[0]+"&loginType=2"
      }
    },
    //返回上一级页面
    back() {
      this.$router.go(-1);
      this.$destroy();
    },
  }
}



