import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
import Loading from '../../components/loading/loading'
export default {
  name: "gift-paper",
  components:{Scroll,Loading},
  data(){
    return{
      id:"",
      pullup: true,
      dataList:[],
      page:1
    }
  },
  mounted(){

  },
  created(){
    this.id=this.$route.params.id;
    this.getData(this.id)
  },

  methods:{
    searchMore() {
      if (this.dataList.length >= 10) {
        this.$refs.loading.style.display = "flex"
        this.loading = true;
        this.page++;
        axios.get("http://e.shushangsoft.com/customerLoginController.do?method=getMerchantsByAcId&activityId="+id+"&page="+this.page+"&rows=10&loginType=2").then((res)=>{
          if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
            alert("登录信息过期")
            this.$router.replace({
              path: '/login',query:{}
            });
          }
          else {
            if(res.data.dataList.length!==0){
              this.$refs.loading.style.display = "none"
              this.dataList.push(res.data.dataList)
            }
            else {
              console.log("没数据了")
              this.page=1
            }
          }
        })
      }
      else {
        this.$refs.loading.style.display = "none"
        this.loading = false;
      }
    },
    getImg(item){
      if(this.dataList.length!==0){
        var imgId=item.imageIds.split(",");
        return "http://e.shushangsoft.com/fileController.do?method=showimage&id="+imgId[0]+"&loginType=2"
      }
    },
    getData(id){
      axios.get("http://e.shushangsoft.com/customerLoginController.do?method=getMerchantsByAcId&activityId="+id+"&page=1&rows=10&loginType=2").then((res)=>{
        console.log(res.data)
        this.$refs.loading.style.display = "none"
        if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
          alert("登录信息过期")
          this.$router.replace({
            path: '/login',query:{}
          });
        }else {
          this.$refs.loading.style.display = "none"
          this.dataList=res.data.dataList
        }
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item.merchantId}`
      })
    },
    //返回上一级页面
    back() {
      this.$router.go(-1);
      this.$destroy();
    },
  }
}



