import SearchBox from '../../components/search-box/search-box'
import Scroll from "../../components/scroll/scroll"
import axios from 'axios'
  export default {
      name: "search",
    components:{SearchBox,Scroll},
    data(){
        return{
          query: '',
          dataList:''
        }
    },
    methods:{
      //搜索发生变化，得到搜索的文字
      onQueryChange(query) {
        this.query = query
      },
      selectItem(item){
        this.$router.push({
          path: `/shop/${item.merchantId}`
        })
      },
      //返回上一级页面
      back() {
        this.$router.go(-1);
        this.$destroy();
      },
      getImg(item){
        var imgId=item.imageIds.split(",");
        return "http://e.shushangsoft.com/fileController.do?method=showimage&id="+imgId[0]+"&loginType=2"
      },
    },
    watch:{
      query(newQuery){
      // 请求网络
        axios.get("http://e.shushangsoft.com/customerLoginController.do?method=getMerchantsByIndustryCategoryId&merchantName="+newQuery+"&loginType=2").then((res)=>{
          this.dataList=res.data.dataList
        })
      }
    },
  }



