import Slider from '../../components/slider/slider'
import Tab from '../../components/public/navbar'
import Loading from '../../components/loading/loading'
import axios from 'axios'
import CityData from '../../common/js/cityData'

export default {
name: "home2",
components: {Scroll, Slider, Tab, Loading, Swiper, SwiperItem},
data() {
return {
swiperItemIndex: 0,
pullup: true,
hasMore: true,
loading: false,
code: "",
firstCode: "",
activityData: [],
city: "",
isFirst: false,
location: {},
firstCity: "",
cityData: [],
iconMap: [],
page: 1,
imgUrls: [
'http://gw.alicdn.com/imgextra/i4/164/TB2RhntAFGWBuNjy0FbXXb4sXXa_!!164-0-luban.jpg',
'http://img.alicdn.com/imgextra/i2/52/TB2IYJEjyMnBKNjSZFzXXc_qVXa_!!52-0-luban.jpg',
'http://gw.alicdn.com/imgextra/i1/127/TB2YC6RjrZnBKNjSZFKXXcGOVXa_!!127-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i4/87/TB27UR8i5MnBKNjSZFCXXX0KFXa_!!87-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i2/190/TB2mLdhA29TBuNjy0FcXXbeiFXa_!!190-0-lubanu.jpg',
'http://img.alicdn.com/imgextra/i2/52/TB2IYJEjyMnBKNjSZFzXXc_qVXa_!!52-0-luban.jpg',
'http://gw.alicdn.com/imgextra/i1/127/TB2YC6RjrZnBKNjSZFKXXcGOVXa_!!127-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i4/87/TB27UR8i5MnBKNjSZFCXXX0KFXa_!!87-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i2/190/TB2mLdhA29TBuNjy0FcXXbeiFXa_!!190-0-lubanu.jpg'
],
}
},
computed: {
curCity() {
return this.$store.state.curCity;
},
},
mounted() {
this.$nextTick(() => {
this.$refs.scroll.refresh();
})
},
created() {
console.log(localStorage.getItem("login"))
this.code = this.$store.state.code;
this.isFirst = this.$store.state.isFirst;
console.log(this.code)
console.log(this.isFirst)
this.cityData = CityData;
if (!this.isFirst) {
console.log("wocao1")
this.getLocation();
}




this.getCategoryData();
this.city = this.$store.state.c

},

activated() {
this.code = this.$store.state.code;
this.isFirst = this.$store.state.isFirst;
console.log(this.isFirst)
if (this.isFirst) {
this.getActivityData(this.code);
console.log("回来了")
}
},

methods: {
getLocation() {
var geolocation = new BMap.Geolocation()
geolocation.getCurrentPosition((r) => {
if (r.point) {

var myGeo = new BMap.Geocoder();

myGeo.getLocation(new BMap.Point(r.longitude, r.latitude), (res) => {
if (res) {
this.firstCity = res.addressComponents.city;
for (var i = 0; i < this.cityData.length; i++) {
for (var j = 0; j < this.cityData[i].list.length; j++) {
if (this.firstCity === this.cityData[i].list[j].name) {
this.firstCode = this.cityData[i].list[j].zip
break;
}
}
break;
}
this.getActivityData(this.firstCode);
}
});
}
}, {enableHighAccuracy: true})
},

returnImg(item) {
switch (item.industryCategoryName) {
case "地板墙砖":
return require("../../common/image/diban.png");
break;
case "卫浴浴洁具":
return require("../../common/image/weiyu.png");
break;
case "厨房设施":
return require("../../common/image/chufang.png");
break;
case "家具软装":
return require("../../common/image/jiaju.png");
break;
case "五金水暖":
return require("../../common/image/shuinuan.png");
break;
case "灯饰开关":
return require("../../common/image/dengju.png");
break;
case "油漆涂装":
return require("../../common/image/tuzhuang.png");
break;
case "其它":
return require("../../common/image/all.png");
break;
case "门窗":
return require("../../common/image/chuanghu.png");
break;
}
},

getImage(item) {
var imgId = item.coverImageId;
return "http://e.shushangsoft.com/fileController.do?method=showimage&id=" + imgId + "&loginType=2"
},
selectCity() {
this.$router.push({
path: `/selectcity`
})

},
toAllCategory(){
this.$router.push({
path: `/allcategory`
})
},
search() {
this.$router.push({
path: `/search`
})
},
loadImage() {



if (!this.checkloaded) {
this.checkloaded = true
this.$refs.scroll.refresh();
}
},
searchMore() {
if (this.activityData.length >= 10) {
this.$refs.loading.style.display = "flex"
this.loading = true;




this.page++;
var url = "http://e.shushangsoft.com/customerLoginController.do?method=getActivitys&page=" + this.page + "&rows=10&loginType=2&shiCode=" + this.firstCode
console.log(url);
axios.get(url).then((res) => {

console.log(res.data);
if (res.data.ret === "-991" || res.data.ret === "-992" || res.data.ret === "-993") {
alert("登录信息过期")
this.$refs.loading.style.display = "none"
this.$store.commit('updateFirst', false);
this.$router.replace({
path: '/login', query: {}
});
this.$destroy();
}
else {
if (res.data.dataList.length !== 0) {
this.$refs.loading.style.display = "none"
this.activityData.push(res.data.dataList);
this.$store.commit('updateFirst', false);
}
else {
this.page = 1;
console.log("没数据了")
}
}
})
this.$refs.loading.style.display = "none"
this.loading = false;

}
else {
this.$refs.loading.style.display = "none"
this.loading = false;
}
},
getDate(time) {
const date = new Date(time);
const Y = date.getFullYear() + '-';
const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
const D = date.getDate() + ' ';
const h = date.getHours() + ':';
const m = date.getMinutes() + ':';
const s = date.getSeconds();
return Y + M + D + h + m + s;
},
getCategoryData() {
axios.get("http://e.shushangsoft.com/industryCategoryController.do?method=getIndustryCategorys&loginType=2").then((res) => {
console.log(res.data);
if (res.data.dataList.length > 9) {
for (var i = 0; i < 9; i++) {
this.iconMap.push(res.data.dataList[i])
}
}
else {
this.iconMap = res.data.dataList;
console.log(this.iconMap)
}
})
},
getActivityData(code) {
const url = "http://e.shushangsoft.com/customerLoginController.do?method=getActivitys&page=1&rows=10&loginType=2&shiCode=" + code
console.log(url);
axios.get(url).then((res) => {
this.$refs.loading.style.display = "flex"
if (res.data.ret === "-991" || res.data.ret === "-992" || res.data.ret === "-993") {
alert("登录信息过期")
this.$refs.loading.style.display = "none"
this.$store.commit('updateFirst', false);
this.$router.replace({
path: '/login', query: {}
});
this.$destroy();
}
else {
console.log(res.data);
this.$refs.loading.style.display = "none"
this.activityData = res.data.dataList;
this.$store.commit('updateFirst', false);
}
})
},

selectItem(item) {
this.$router.push({
path: `/activityshoplist/${item.activityId}`
})
},
selectCgtegory(item) {
this.$router.push({
path: `/categoryshoplist/${item.industryCategoryId}`
})
},
},
beforeDestroy() {
this.$store.commit("updateIsLogin", true);
}
}



