
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
export default {
name: "gift-paper",
data(){
return{
scanImg:false,
qrcodeImgUrl:"",
allPaper:[],
activityId:"",
unUsePaper:[
"https://s1.ax1x.com/2018/07/28/PUdXy8.png",
],
paperIndex:0
}
},
created(){
this.getData()
},

methods:{
showCode(item){
this.$router.push({
path: `/game`
})
},
getData(){
axios.get("http://e.shushangsoft.com/customerLoginController.do?method=getMyGameCoupon&page=1&rows=5&loginType=2").then((res)=>{
console.log(res)
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("登录信息过期")
this.$router.replace({
path: '/login',query:{}
});
}else {
if(res.data.dataList.length!==0){
this.allPaper=res.data.dataList;
this.activeId=res.data.dataList[0].activityId;
this.$store.commit('updateActivityId',  this.activeId)
}
}
})
},

back() {
this.$router.go(-1);
this.$destroy();
},
}
}



